<template>
  <el-container class="layout">
    <el-aside
      :style="{
        backgroundColor: themecolor,
        width: isCollapse ? 'auto' : '200px'
      }"
    >
      <div class="title"><span>Bing</span></div>
      <el-menu
        :default-openeds="['1', '3']"
        :background-color="themecolor"
        active-text-color="#bacf65"
        text-color="#fff"
        :collapse="isCollapse"
        router
      >
        <el-submenu index="1">
          <template slot="title"
            ><i class="el-icon-s-custom"></i
            ><span slot="title">账号管理</span></template
          >
          <el-menu-item index="/rolelist"> 角色管理</el-menu-item>
          <el-menu-item index="1-2">账号管理</el-menu-item>
        </el-submenu>
        <el-menu-item index="/house">
          <i class="el-icon-office-building" /><span slot="title"
            >客房管理</span
          >
        </el-menu-item>

        <el-submenu index="3">
          <template slot="title"
            ><i class="el-icon-user"></i
            ><span slot="title">客户管理</span></template
          >
          <el-menu-item index="3-1"
            ><span slot="title">角色管理</span></el-menu-item
          >
          <el-menu-item index="3-2"
            ><span slot="title">账号管理</span></el-menu-item
          >
        </el-submenu>
        <el-submenu index="4">
          <template slot="title"
            ><i class="el-icon-setting"></i
            ><span slot="title">权限管理</span></template
          >
          <el-menu-item index="4-1"
            ><span slot="title">权限管理</span></el-menu-item
          >
          <el-menu-item index="4-2"
            ><span slot="title">账号管理</span></el-menu-item
          >
        </el-submenu>
      </el-menu>
    </el-aside>
    <el-container>
      <el-header :style="{ backgroundColor: themecolor }">
        <div @click="isCollapse = !isCollapse" style="font-size: 25px">
          <i :class="isCollapse ? 'el-icon-s-unfold' : 'el-icon-s-fold'" />
        </div>
        <el-menu
          :default-active="index1"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          :background-color="themecolor"
          active-text-color="#fbb929"
          text-color="#fff"
          router
        >
          <el-menu-item index="1">
            <i class="el-icon-s-home" />主页</el-menu-item
          >
          <el-submenu index="2">
            <template slot="title"><i class="el-icon-setting" />主题</template>
            <el-menu-item
              @click="themeChange(item.value)"
              v-for="(item, index) in theme"
              :key="index"
            >
              {{ item.name }}
            </el-menu-item>
          </el-submenu>
          <el-menu-item index="/message">
            <i class="el-icon-message" /> 邮箱
          </el-menu-item>
          <el-menu-item index="2">
            <i class="el-icon-s-comment" /> 消息
          </el-menu-item>
          <el-submenu index="4">
            <template slot="title"
              ><i class="el-icon-user-solid" />管理员</template
            >
            <el-menu-item index="4-1"> 修改密码 </el-menu-item
            ><el-menu-item @click="exit()">退出 </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import { mapState } from "vuex";
import { mapMutations } from "vuex";

export default {
  async created() {
    let username = localStorage.getItem("username");
    if (username) {
      let resuls = await this.axios.get("/api/list", {
        params: {
          id: username
        }
      });
      let user_info = resuls.data[0];
      this.setusers({ username, user_info });
      this.res_list.push(user_info);
    } else {
      this.$router.push("/");
    }
  },
  computed: {
    ...mapState("theme", ["theme"]),
    ...mapState("users", ["users"])
  },
  data() {
    return {
      res_list: [],
      index1: "1",
      isCollapse: false,
      themecolor: localStorage.getItem("themecolor") || "#ede3e7"
    };
  },
  methods: {
    ...mapMutations("users", ["setusers"]),
    exit() {
      this.$router.push("/");
      sessionStorage.clear();
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    themeChange(color) {
      this.themecolor = color;
      localStorage.setItem("themecolor", this.themecolor);
    }
  }
};
</script>

<style lang="less" scoped>
.layout {
  width: 100%;
  height: 100vh;
  .el-header {
    padding: auto 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.el-aside {
  .title {
    line-height: 38px;
    font-family: _GB2312 KaiTi_GB2312;
    margin: 10px 10px;
    color: #fff;
    text-align: center;
    border: 1px solid white;
    border-radius: 4px;
  }
  .el-menu:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
    .el-submenu {
      width: 200px;
    }
  }
}
</style>
